.wrapper
{
    font-family: 'Arial-Bold', Arial, sans-serif;
    font-weight: 400;
}
.container
{
    max-width: 1600px;
    margin: 0px;
    padding: 0px ;
    /* margin-left: 5vw;
    margin-right: 25vw; */
}
.header
{
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 5;
}
.header::before
{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 12vh;
    background-color: rgb(255, 255, 255);
    z-index: 2;
}

.header__body{
    position: relative;
    display: flex;
    justify-content: space-between;
    height: 12vh;
    align-items: center;
}
.header__logo
{
    margin-top: 0%;
    flex: 0 0 7%;
    position: relative;
    z-index: 3;
    margin-left: 5%;
}

.header__logo img{
    max-width: 100%;
    height: auto;
}
.header__list{
    display: flex;
    position: relative;
    z-index: 2;
    margin-right: 35vh;
    margin-left: auto;
}
.header__list li{
    list-style: none;
    margin: 0px 20px 0px 30px;
    display: block;
}
.header__link{
    color: #101c54;
    font-weight:600;
    font-size: 2.5vh;
    text-decoration: none;
    transition: 0.5s ease;
}

.header__link.active {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-weight: bold;
}

.header__link.active::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 150%;
    height: 150%;
    background-color: #101c54;
    border-radius: 50px;
    z-index: -1;
    transition: width 0.3s, height 0.3s; /* Add transition */
}

    

.header__burger
{
    display: none;
}
@media (max-width:767px){
    .header__body
    {
        height: 50px;
    }
    .header__logo
    {
        flex: 0 0 40px;
    }
    .header__burger{
        display: block;
        position: relative;
        width: 30px;
        height: 20px;
        position: relative;
        z-index: 3;
        margin-right: 5%;
    }
    .header__burger::after,.header__burger::before{
        content: '';
        background-color: rgb(0, 0, 0);
        position: absolute;
        width: 100%;
        height: 2px;
        left: 0;
        transition: all 0.3s ease 0s;
    }
    .header__burger::before{
        top: 0;
    }
    .header__burger::after{
        bottom: 0;
    }
    .header__burger span {
        position: absolute;
        background-color: black;
        left: 0;
        width: 100%;
        height: 2px;
        top:9px;
        transition: all 0.3 ease 0s;
    }
    .header__burger.active::before{
        transform:rotate(45deg);
        top: 9px;

    }
    .header__burger.active::after{
        transform:rotate(-45deg);
        bottom: 9px;
    }
    .header__burger.active span
    {
        transform: scale(0);
    }
    .header__menu{
        position: fixed;
        top: -100%;
        left: 0;
        width: 100%;
        height: 90%;
        transition: all 0.3s ease 0s;
        background-color: rgb(255, 255, 255);
        padding: 70px 10px 20px 10px;
    }
    .header__menu.active{
        top: 0;
    }
    .header__list{
        display: block;
        margin-top: 10%;
        width: 100%;
    }
    .header__list li{
        margin: 0px 0px 20px 0px;
    }
    .header::before{
        height: 50px;
    }
    .header__link.active::after {
       display: none;
    }
    .header__link.active{
        display: block;
        color: #101c54;
        font-weight:600;
        font-size: 2.5vh;
        text-decoration: none;
        transition: 0.5s ease;
        margin: 0px 0px 20px 0px;
    }
}
